import React from 'react';
import Image from 'next/image';
import { filtered_top_data } from '@/app/spotify_utils/types';

type filtered_top_data_props = filtered_top_data & {
	className?: string,
}

function TopItem({ className = '', name, external_urls, artists, album }: filtered_top_data_props) {
	return (
		<div key={name} className={`flex items-center gap-4 ${className}`}>
			{/* Album Cover */}
			<div className="w-[100px] shrink-0">
				<Image src={album.images[1].url} height={100} width={100} alt="album logo" />
			</div>

			{/* Song Info */}
			<div className="w-64">
				<a href={external_urls.spotify} target="_blank" rel="noopener noreferrer">
					<b><h1 className="text-xl">{name}</h1></b>
				</a>
				{artists.map(({ external_urls, name }) => <a href={external_urls.spotify} target='_blank' key={name}><h5>{name}</h5></a>)}
			</div>
		</div>
	)
}

export default TopItem
